import React from 'react';
import {CSSTransition} from 'react-transition-group';
import victoryImg from '../images/victory-min.png';
import shine from '../images/victory_shine-min.png';
const VictoryPopup = ({victory, tryAgain, goToMenu}) => {
    return(
        <CSSTransition classNames="popup__modal_result" unmountOnExit in={victory} timeout={300}>
            <div className="popup popup_victory">
                <div className="popup__overlay popup__overlay_result">
                    <img src={shine} alt="shine" className="popup__shine"/>
                    <div className="modal modal_result">
                        <div className="modal__frame">
                            <div className="modal__result-img-block">
                                <img className="modal__result-img" src={victoryImg} alt="victory"/>
                            </div>
                            
                            {/* <div className="modal__text-block modal__text-block_result">
                                <img className="modal__result-img" src={victoryImg} alt="victory"/>
                            </div> */}
                            <div className="modal__buttons-block modal__buttons-block_result">
                                <button className="button button_brown button_result" onClick={() => goToMenu()}>Menu</button>
                                <button className="button button_orange button_result" onClick={() => victory ? tryAgain() : null}>Start again</button>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
{/* 
            <div className="popup_victory">
                <CSSTransition classNames="popup__modal_result" unmountOnExit in={victory} timeout={300}>

                </CSSTransition>
            </div> */}
        </CSSTransition>
    )
}

export default VictoryPopup;