import React from 'react';
import {CSSTransition} from 'react-transition-group';
import gameOverImg from '../images/game-over.png';
import devilGameOver from '../images/DEVIL_game-over-min.png';

const GameOverPopup = ({gameOver, tryAgain, goToMenu}) => {
    return(
        <CSSTransition classNames="popup__modal_result" unmountOnExit in={gameOver} timeout={300}>
            <div className="popup popup_gameOver">
                <div className="popup__overlay popup__overlay_result">
                    <img src={devilGameOver} alt="devilGameOver" className={`devil_result`}/>
                    <div className="modal modal_result">
                        <div className="modal__frame modal__frame_gameOver">
                            <div className="modal__result-img-block">
                                <img className="modal__result-img modal__result-img_gameOver" src={gameOverImg} alt="victory"/>
                            </div>
                            <div className="modal__buttons-block modal__buttons-block_result">
                                <button className="button button_brown button_result" onClick={() => goToMenu()}>Menu</button>
                                <button className="button button_orange button_result" onClick={() => gameOver ? tryAgain() : null}>Start again</button>
                            </div>
                        </div>


                    </div>
                </div>
            </div>
        </CSSTransition>
    )
}

export default GameOverPopup;